define('watch-chrono/router', ['exports', 'ember', 'watch-chrono/config/environment'], function (exports, Ember, config) {

  'use strict';

  var Router = Ember['default'].Router.extend({
    location: config['default'].locationType
  });

  Router.map(function () {
    this.route('report');
    this.route('options');
    this.route('main');
  });

  exports['default'] = Router;

});